import { GetPictureIdList } from '../../network/picture/PictureRequests'
import { GetLatestArticle } from '../../network/read/ReadRequest'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
import swipeItem from './swipeItem.vue'
const REQUEST_INST = 10
export default{
  components: {
    swiper,
    swiperSlide,
    swipeItem
  },
  computed: {
    swiper () {
      return this.$refs.mySwiper.swiper
    }
  },
  mounted: function () {
    if (this.pictureIds.length !== REQUEST_INST) {
      this.initData()
    }
  },
  data () {
    return {
      pictureIds: [],
      essays: [],
      questions: [],
      swiperOption: {
        pagination: '.swiper-pagination',
        paginationClickable: true,
        effect: 'cube',
        initialSlide: 0,
        centeredSlides: true,
        slidesPerView: 'auto',
        crossFade: false
      },
      swiperEssayOption: {
        pagination: '.swiper-pagination',
        paginationClickable: true,
        effect: 'fade',
        initialSlide: 0,
        centeredSlides: true,
        slidesPerView: 'auto',
        crossFade: true
      }
    }
  },
  methods: {
    initData: function () {
      let self = this
      new GetPictureIdList().start(function (data) {
        self.pictureIds = data.data.data
      })
      new GetLatestArticle().start(function (data) {
        console.log(data.data.data)
        self.essays = data.data.data.essay
        self.questions = data.data.data.question
      })
    },
    routeEassy: function (id) {
      this.$router.push({
        name: 'readDetail',
        params: {
          'id': id
        }
      })
    },
    routeQuestion: function (id) {
      this.$router.push({
        name: 'questionDetail',
        params: {
          'id': id
        }
      })
    }
  }
}



