
import { GetPictureDetail } from '../../network/picture/PictureRequests'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
directives: {
TransferDom
},
components: {
XDialog
},
mounted: function () {
this.initData()
},
data () {
return {
show: false,
hideBlur: true,
id: this.$attrs.data,
pictureDetail: {
hp_content: '',
hp_img_url: '',
hp_author: '',
text_authors: ''
}
}
},
methods: {
initData: function () {
let self = this
let params = this.id
new GetPictureDetail(params).start(function (data) {
self.pictureDetail = data.data.data
})
}
}
}



